import React, { Component } from 'react';
// import FadeLetters from 'FadeLetters';

export default class Hero extends Component {
  render() {
    return (
      <section id="WELCOME" className="module">
        <iframe className="module-bcgVideo" src="https://player.vimeo.com/video/307136859?background=1" frameBorder="0"></iframe>
        <div className="hero col-12">
          <div className="hero-content">
            <div className="hero-logo" style={{height: "120px"}}></div>
            <h1 className="hero-title" data-fadeletters>Nau Mai. Welcome.</h1>
            <img src="./images/Lozenge.svg" alt="" className="hero-lozenge"/>
          </div>
        </div>

        {/* <FadeLetters/> */}
      </section>
    );
  }
}


