import React, { Component } from 'react';

export default class FullScreen extends Component {
  render() {
    const bcgImg = this.props.data.bcgImg,
          align = this.props.data.align,
          title = this.props.data.title,
          paragraph = this.props.data.paragraph,
          vimeo = this.props.data.vimeo,
          titleNational = this.props.data.titleNational;

    return (
      <section id={this.props.id} className="module fullScreen" style={{backgroundImage:`url(${bcgImg})`}}>
        {vimeo && (
          <iframe className="module-bcgVideo" src={`https://player.vimeo.com/video/${vimeo}?background=1`} frameBorder="0"></iframe>
        )}
        <div className="col-4 col-s-12">
          <div className={`module-content -align-${align}`}>
            {title && title.map(line => {
              return(
                <h2 className={`module-title ${titleNational ? '-national' : null}`} data-fadeletters>{line}</h2>
              )
            })}
            
            {paragraph ? (
              <p className="module-paragraph" data-fadeletters>{paragraph}</p>
            ) : (
              null
            )}
            
          </div>
        </div>
        <div className="col-8 col-s-0"></div>
      </section>
    );
  }
}


