import React, { Component } from 'react';
import FullScreen from './FullScreen';
import SplitScreen from './SplitScreen';
import FadeLetters from './FadeLetters';

export default class BrandGuidelines extends Component {
  state = {
    fadeLettersDone: false
  }

  fadeLettersStart(){
    this.setState({
      fadeLettersDone: false
    })
  }

  fadeLettersEnd(){
    this.setState({
      fadeLettersDone: true
    })
  }

  render() {
    return(
      <main>
        <div className="sideNav-border"></div>

        <FullScreen data={{bcgImg: './images/guidelines/01.jpg', title: ["Pure Pkati"], paragraph: "Pure Pkati is our custom typeface created by type designer Kris Sowersby and carved into native kauri timber by artist Rangi Kipa. A unique cross&#8209;collaboration between two of New Zealands leading contemporary artists.", align: "bottom"}}/>

        <FullScreen data={{bcgImg: './images/guidelines/02.jpg', titleNational: true, title: ["Carved by hand"], paragraph: "Whakairo (traditional Mori carving) is a customary art-form that brings warmth, texture and meaning to sculpted forms. By carving our evolved typeface from native timber, then inking and printing the letterforms, the grain of the wood adds texture and adds unique character in a quintessentially New Zealand way.", align: "bottom"}}/>

        <SplitScreen data={{sides:[
          {title:["The story behind the three Nihoniho notches"], paragraphs:["What makes the Pure Pkati unique is the three simple notches, or Pkati in the nihoniho style that were inspired by the story of Toi Kai Rakau searching across the Pacific for his grandson Whatonga who was lost at sea.","Toi bound a stick of wood with three lashings and set it adrift in the hope it would reach him. The three lashings represented three questions: Where are you? How are you? When will you be back? It is a rich story of love and care between whnau members and reminds us of what it means to embrace visitors to New Zealand as part of our whnau."], align: "middle", theme: "-dark-theme"},
        {align: "middle", image: "/guidelines/03-50-50.jpg"}
        ]}}/>

        <FullScreen data={{vimeo: "326475588"}}/>

        <section className="module fullScreen coreElements -light-theme">
          <h2 className="coreElements-subtitle module-subtitle -bold">The core elements that define our distinctly New Zealand identity</h2>
          <div className="coreElements-thirds">
            <div className="coreElements-thirds-item">
              <img src="./images/guidelines/04/1.png" alt="" className="coreElements-thirds-img"/>
              <h3 className="coreElements-thirds-title">The logomark</h3>
              <p className="coreElements-thirds-subtitle">Our core logomark and ifentifier - one of the cornerstones of our brand.</p>
            </div>

            <div className="coreElements-thirds-item">
              <img src="./images/guidelines/04/2.png" alt="" className="coreElements-thirds-img"/>
              <h3 className="coreElements-thirds-title">Pure Pkati typeface</h3>
              <p className="coreElements-thirds-subtitle">Our bespoke type3face for our new brand campaign.</p>
            </div>

            <div className="coreElements-thirds-item">
              <img src="./images/guidelines/04/3.png" alt="" className="coreElements-thirds-img"/>
              <h3 className="coreElements-thirds-title">The bookmark</h3>
              <p className="coreElements-thirds-subtitle">A versatile graphic device that helps anchor our logomark and hero our favourite destinations.</p>
            </div>
          </div>
        </section>

        <FullScreen data={{bcgImg: './images/guidelines/05.jpg', title: ["Our iconic logomarks  from this place"], align: "bottom"}}/>

        <FullScreen data={{vimeo: "326481464"}}/>

        <section className="module fullScreen filler" style={{paddingTop: "24vh", paddingBottom: "24vh"}}>
          <img src="./images/fillers/08-Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{bcgImg: './images/guidelines/09.jpg', title: ["Typography that tells a richer story"], align: "top"}}/>

        <FullScreen data={{vimeo: "326706256"}}/>

        <section className="module fullScreen filler">
          <img src="./images/fillers/11-Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{vimeo: "326707941"}}/>

        <section className="module fullScreen filler">
          <img src="./images/fillers/11-2 Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{bcgImg: './images/guidelines/12.jpg', title: ["A colour palette thats distinctly us"], align: "top"}}/>

        <section className="module fullScreen filler">
          <img src="./images/fillers/13-Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{vimeo: "327864487"}}/>

        <FullScreen data={{bcgImg: './images/guidelines/14.jpg', title: ["Imagery that puts you in the picture"], align: "top"}}/>

        <section className="module fullScreen filler">
          <img src="./images/fillers/15-Content.png" alt="" className="filler-img"/>
        </section>

        <section className="module fullScreen filler">
          <img src="./images/fillers/16-Content.png" alt="" className="filler-img"/>
        </section>

        <section className="module fullScreen filler">
          <img src="./images/fillers/17-Content.png" alt="" className="filler-img"/>
        </section>

        <section className="module fullScreen filler">
          <img src="./images/fillers/18-Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{bcgImg: './images/guidelines/19.jpg', title: ["Graphic devices that guide your way"], align: "top"}}/>

        <section className="module fullScreen filler">
          <img src="./images/fillers/20-Content.png" alt="" className="filler-img"/>
        </section>

        <section className="module fullScreen filler">
          <img src="./images/fillers/21-Content.png" alt="" className="filler-img"/>
        </section>

        <section className="module fullScreen filler">
          <img src="./images/fillers/22-Content.png" alt="" className="filler-img"/>
        </section>

        <FullScreen data={{bcgImg: './images/guidelines/23.jpg', title: ["Ka kite an"], paragraph: "See you again soon. For all enquiries and questions regarding the Tourism New Zealand Brand Tohu, please contact jamie.reid@tnz.govt.nz", align: "bottom"}}/>

        <section className="module fullScreen filler"></section>

        <FadeLetters start={this.fadeLettersStart.bind(this)} end={this.fadeLettersEnd.bind(this)}/>
      </main>
    )
  }
}


