import React, { Component } from 'react';
import Nav from './Nav';
import Hero from './Hero';
import FullScreen from './FullScreen';
import SplitScreen from './SplitScreen';
import SplitAccordian from './SplitAccordian';
import QuoteCarousel from './QuoteCarousel';
import PeoplePlaceCarousel from './PeoplePlaceCarousel';
// import FadeInSections from './FadeInSections';
import FadeLetters from './FadeLetters';
import ToolTips from './ToolTips';

export default class BrandStory extends Component {
  state = {
    fadeLettersDone: false
  }

  fadeLettersStart(){
    this.setState({
      fadeLettersDone: false
    })
  }

  fadeLettersEnd(){
    this.setState({
      fadeLettersDone: true
    })
  }
  
  render() {
    return(
      <main>
        <Nav/>

        <Hero/>

        <FullScreen data={{vimeo: "307136881", title: ["The KauPAPA"], paragraph: "Sharing with the world what a 100% Pure New Zealand  experience feels like. These guidelines are designed to help you do&nbsp;that.", align: "bottom"}}/>

        <SplitScreen data={{sides:[{quote:{text:"Enriching New Zealand is about giving more than we are taking away.", source:["Stephen England-Hall,","CEO, Tourism New Zealand"]}, align: "middle", theme: "-dark-theme"},{align: "middle", image: "stephen_1080.png"}]}}/>

        <FullScreen id={"INSIGHT"} data={{bcgImg: "./images/Insight_IMG_1.jpg", vimeo: "307136834", title: ["The insight"], paragraph: "The experience that our manuhiri / visitors are looking for.", align: "bottom"}}/>

        <SplitScreen data={{sides:[{title: ["From an emotional perspective."], paragraphs: ["Im a traveller, not a tourist. l want to immerse myself in the culture of the places I go. In a world thats becoming ever more commercialised and jaded, where can I reconnect with whats&nbsp;real?"], align: "middle", theme: "-dark-theme"}, {title: ["From a practical perspective."], paragraphs: ["If I am going to travel all that way I want to make the most of every moment I have. Experiencing all of the best things the place has to offer while finding unexpected experiences along the&nbsp;way."], align: "middle", theme: "-light-theme"}]}}/>

        <QuoteCarousel data={{title:"Quotes from our International Travellers", quotes:[
          "Wherever go you, you feel like you belong.",
          "New Zealand is a very inclusive place.",
          "People actually invite you into their homes without a second thought... its these kinds of experiences that blow your mind.",
          "Youre treated as a local. You experience their natural hospitality... it means real experiences.",
          "I want to open myself to the whole experience, not feel like an outsider looking in and just watching."
          ]
        }}/>

        <FullScreen id={"BRAND_TRUTH"} data={{vimeo: "307136812", title: ["Brand truth"], paragraph: "The brand truth at the heart of a 100% Pure New Zealand experience.", align: "bottom"}}/>

        <SplitScreen data={{sides:[{bigTitle: "Feel the embrace of this place", align: "middle", theme: "-light-theme"}, {paragraphs: ["There are few places in the world that welcome you in like New Zealand does. Connecting you quickly and deeply to the places you visit and the people you meet. Where the concept of feeling part of the family is extended easily and quickly to all who are open to it. Where our range of stunning landscapes are within easy reach, drawing you in wherever you travel. A place that embraces all who experience it, opening you up to rich and diverse&nbsp;experiences."], align: "middle", theme: "-light-theme"}]}}/>

        <PeoplePlaceCarousel data={{image:"./images/carousel.svg"}}/>

        <FullScreen id={"BRAND_VOICE"} data={{bcgImg: "./images/Brand_Voice_Img.jpg", title: ["Brand voice"], paragraph: "Our unique kiwi voice. Tngata ki te tngata. People to People.", align: "bottom"}}/>

        <SplitAccordian data={
          [
            {
              paragraphs:["A shift to a more conversational connection. Where our voice and character is that of friends standing face to face. The people of Aotearoa New Zealand, sharing their stories with the world, and the world sharing them back. A tone of voice that speaks to you like youre already right here in our&nbsp;hearts."], 
              align: "middle", theme: "-light-theme"
            },
            {
              accordian:[
                {title: "A Conversation.",paragraphs:["Were not speaking to people, were speaking with them. A conversational style that comes from standing on the same level, eye to eye, face to face."]}, 
                {title: "An open invitation.",paragraphs:["Focusing on being open, warm and welcoming, less on illustrating with evocative language. Guiding rather than explaining."]}, 
                {title: "A unique point of view.",paragraphs:["Sharing stories of how we see New Zealand through our own eyes. Individual, personal, unique perspectives, never a one-size-fits-all description."]}
              ], 
              align: "middle", theme: "-light-theme"
            }
          ]
        }/>

        {/* <SplitScreen data={{sides:[{image:"Tangata_Img.jpg", align: "middle"}, {title: ["Tngata ki te tngata. People to people."], paragraphs: ["A shift from us as a tourism organisation speaking on high to a more conversational connection. Where our voice and character is that of friends standing face to face. Our people, the people of Aotearoa New Zealand, sharing their stories with the world, and the world sharing them back."], align: "middle", theme: "-dark-theme"}]}}/> */}

        <SplitAccordian id={"KIWI"} vimeo={"314164474"} data={
          [
            {title: "KIWI SPIRIT", paragraphs:["Our unique kiwi spirit. The cultural truths that shape the visitors' journey."], align: "bottom"}, 
            {accordian:[
              {title: "Manaakitanga", paragraphs:[
                "A deeper expression of hospitality based on true empathy and connection.",
                "Every person has mana. Your mana is affirmed by the way people speak of you based on how you treat them.",
                "Grounded in Manaakitanga",
                "This guides how we look after visitors through the extension of hospitality and respect. It means you act not for financial gain but for how others speak (aki) of you. This means we also err on the side of generosity towards our guest or host.",
                "It works both ways, understanding the tikanga of the country you are visiting and mindful consideration. You are welcome forever."
              ]}, 
              {title: "Tiaki", paragraphs:[
                "Our duty of care for people and place.",
                "We see New Zealand as a gift from the gods, and we are simply the guardians of this land for the next generation.",
                "Grounded in Kaitiakitanga",
                "Our duty of care for people and place as kaitiaki (guardians of our land) shapes how we look after our visitors and balances this with looking after our place.",
                "Seeing New Zealand through the eyes of those who love it helps our visitors understand how and why we do this (creating shared values). They then also become kaitiaki while they are here."
              ]}, 
              {title: "Whnau", paragraphs:[
                "Embracing visitors as family.",
                "Whakawhanaungatanga means we are active in forming whnau relationships and personal connections with our visitors.",
                "An active act to find points of commonality that can bind you together. It is not about the value of tourism, it is about the value of the person. We believe if we treat visitors like family, they tell their family and friends and they also visit. Powerful advocacy and stories that travel the world.",
                "Once you have visited our place you are part of the whnau, you are welcome forever."
              ]}
            ], align: "bottom"}
          ]
        }/>

        <SplitScreen data={{vimeo:"307136905", sides:[{quote:{text:"The deeper we dig into New Zealand culture, the more we expose its Mori roots.", source:["Karl Wixon,","Cultural Advisor"]}, align: "middle", theme: "-dark-theme"},{image:"karl_1080.jpg", align: "middle"}]}}/>

        <SplitScreen id={"PURENZ"} data={{sides:[{align: "middle", theme: "-dark-theme", logo:"./images/pureNZ.svg"}, {paragraphs: [
          "When we authentically share the stories and experiences of the embrace of this place, then and only then should our 100% Pure New Zealand wordmark be&nbsp;shown.", 
          "When we share the feeling of the experience, not just imagery of a landscape. When we capture our unique kiwi spirit and voice. When we tell a story of both our people and our&nbsp;place."
        ], align: "middle", theme: "-dark-theme"}]}}>
          <footer className="module">
            <div className="col-12">
              <img src="./images/pdf_lozenge.svg" alt="" onClick={() => {
                {/* window.open('./TNZ1128_PDFBrandGuidelinesRefreshPhaseOne_V2.pdf') */}
                let anchorEl = document.createElement('a');
                anchorEl.setAttribute('href', './TNZ1128_PDFBrandGuidelinesRefreshPhaseOne_V9-High.pdf');
                anchorEl.setAttribute('download', 'TNZ1128_PDFBrandGuidelinesRefreshPhaseOne_V9-High');

                anchorEl.style.display = 'none';
                document.body.appendChild(anchorEl);

                anchorEl.click();

                document.body.removeChild(anchorEl);
              }}/>
            </div>
          </footer>
        </SplitScreen>

        <FadeLetters start={this.fadeLettersStart.bind(this)} end={this.fadeLettersEnd.bind(this)}/>

        {(this.state.fadeLettersDone && (
          <ToolTips data={[
            {word: "Tikanga", definition: "Ways and customs"}          
          ]}/>
        ))}
      </main>
    )
  }
}


