import React, { Component } from 'react';

export default class SplitAccordian extends Component {
  active = false;
  
  componentDidMount(){
    this.accordianItems = document.querySelectorAll(".splitAccordian-item");

    this.accordianItems.forEach((item, i) => {
      if(item.onclick === null){
        item.onclick = e => {
          console.log(e.target.hasAttribute('tooltip'));
          if(e.target.hasAttribute('tooltip')) return;
          
          if(this.active !== false && this.active !== i){
            this.toggleAccordian(this.active);
          }
          this.toggleAccordian(i);
        };
      }
    })
  }

  toggleAccordian(i){
    let el = this.accordianItems[i],
        elContent = el.children[2];

    el.classList.toggle("-active");

    if (elContent.style.maxHeight){
      elContent.style.maxHeight = null;
      elContent.style.opacity = null;
      this.active = false;
    } else {
      elContent.style.maxHeight = elContent.scrollHeight + "px";
      elContent.style.opacity = 1;
      this.active = i;
    } 
  }

  render() {
    const data = this.props.data;

    return(
      <section id={this.props.id} className={`module ${data[0].theme}`}>
        {this.props.vimeo && (
          <div className="float"><iframe className="module-bcgVideo" src={`https://player.vimeo.com/video/${this.props.vimeo}?title=0&byline=0&portrait=0&autoplay=1&loop=1&muted=1&controls=0&background=1`} frameBorder="0"></iframe></div>
        )}
        {data.map(col => {
          if(col.accordian){
            return(
              <React.Fragment>
                <div className="col-1 col-s-0"></div>
                <div className="col-5 col-s-12">
                  <div className={`splitAccordian -align-${col.align} ${this.props.vimeo && '-dark-theme'}`}>
                    {col.accordian.map((accordianItem, i) => {
                      return(
                        <div className="splitAccordian-item">
                          <h3 className="splitAccordian-title -bold" data-fadeletters>{`${i + 1}. ${accordianItem.title}`}</h3>
                          <svg viewBox="0 0 48.88 48.88" className="splitAccordian-icon">
                            <polygon points="48.88 21.94 26.94 21.94 26.94 0 21.94 0 21.94 21.94 0 21.94 0 26.94 21.94 26.94 21.94 48.88 26.94 48.88 26.94 26.94 48.88 26.94 48.88 21.94"/>
                          </svg>
                          <div className="splitAccordian-content">
                            {accordianItem.paragraphs.map(paragraph => {
                              return(
                                <p className="module-paragraph splitAccordian-paragraph" data-fadeletters="true">{paragraph}</p>
                              )
                            })}
                          </div>
                        </div>
                      )
                    })}
                  </div>
                </div>
              </React.Fragment>
            )
          } else {
            return (
              <React.Fragment>
                <div className="col-5 col-s-12">
                  <div className={`module-content -align-${col.align} ${col.theme}`}>
                    {col.title ? (
                      <h2 className="module-title" data-fadeletters>{col.title}</h2>
                    ) : (
                      <React.Fragment/>
                    )}

                    {col.paragraphs.map(paragraph => {
                      return(
                        <p className="module-paragraph" data-fadeletters>{paragraph}</p>
                      )
                    })}
                  </div>
                </div>
                <div className="col-1 col-s-0"></div>
              </React.Fragment>
            )
          }
        })}
      </section>
    )
  }
}


