import React, { Component } from 'react';
import { TweenLite, Sine } from "gsap/TweenMax";

export default class Accordian extends Component {
  state = {
    dots: false,
    pressing: false
  }

  start = {};
  moved = 0;
  currSlide = 0;
  timing = 0.4;
  extraMove = 0.25;
  snapToPlace = true;
  snapThreshold = 0.7;

  setUp(accordianEl){
    if(this.accordianEl !== undefined) return;

    this.addEls(accordianEl).then(() => {
      setTimeout(() => {
        this.getValues().then(() => {
          this.addPagination().then(() => {
            if(this.props.data.start){
              this.dotEls[this.currSlide].classList.remove("-active");
              this.currSlide = Math.floor(this.props.data.start * this.dotEls.length);

              if(!this.dotEls.length % 2){ // if even
                if(this.currSlide !== 0){
                  this.currSlide--;
                }
              }
              
              this.moved = this.state.dots[this.currSlide];
              TweenLite.to(this.containEl, 0.7, {x: this.moved + "px"});
              this.dotEls[this.currSlide].classList.add("-active");
            }
            
            window.addEventListener("resize", () => this.resized());
          });
        })
      }, 500);
    })
  }

  resized(){
    clearTimeout(this.resizeTimer);
    this.resizeTimer = setTimeout(() => {
      // reset pagination dots
      this.getValues().then(() => {
        this.addPagination().then(() => {
          this.accordianEl.querySelectorAll(".accordian-dot.-active").forEach(activeDot => {
            activeDot.classList.remove("-active");
          })

          this.currSlide = 0;
          this.release();
        });
      })
    }, 250)
  }

  addEls(accordianEl){
    return new Promise((resolve, reject) => {
      if(accordianEl === null || this.accordianEl !== undefined) reject();

      this.accordianEl = accordianEl;
      this.containEl = this.accordianEl.querySelector(".accordian-contain");
      this.itemEls = this.containEl.childNodes;

      resolve();
    })
  }

  getValues(){
    return new Promise(resolve => {
      let containWidth = 0;

      this.itemEls.forEach(item => {
        containWidth += item.offsetWidth;

        console.log('item.offsetWidth',item.offsetWidth);
      })

      this.containEl.style.width = containWidth + "px";

      this.accordianBox = this.accordianEl.getBoundingClientRect();
      this.containBox = this.containEl.getBoundingClientRect();
      this.stopPoint = this.accordianBox.width - this.containBox.width;
      this.itemBoxes = new Array(this.itemEls.length);

      this.itemEls.forEach((item, i) => {
        this.itemBoxes[i] = item.getBoundingClientRect();
      })

      resolve();
    })
  }

  addPagination(){
    return new Promise(resolve => {
      setTimeout(() => {
        let dots = [0];

        console.log('this.containBox.width',this.containBox.width);
        console.log('this.accordianBox.width',this.accordianBox.width);
        if(this.containBox.width < this.accordianBox.width) return; // see if dots are needed

        dots.push(this.stopPoint); // add end dot

        let slidesVisible = Math.floor(this.accordianBox.width / this.itemBoxes[0].width);
        
        if(slidesVisible < 1) slidesVisible = 1;

        for(let i = 0; i < this.itemEls.length; i++){
          let checkDots = this.checkForAdditionalDot(i, slidesVisible, dots);

          if(checkDots){
            dots.splice(
              dots.length - 1, 
              0, 
              checkDots.middle
            );
          };
        }

        console.log('dots',dots);

        this.setState({
          dots: dots
        })

        this.dotEls = this.accordianEl.querySelectorAll(".accordian-dot");
        this.dotEls[0].classList.add("-active");

        resolve();
      }, 250)
    })
  }

  checkForAdditionalDot(i, slidesVisible, dots){
    let widthLeftToAdd = 0;

    for(let j = i; j <= (i + slidesVisible); j++){
      if(this.itemBoxes[j] === undefined) return false;

      widthLeftToAdd += this.itemBoxes[j].width;
    }

    let latestPointAdded = dots[dots.length - 2],
        visibleSlides = 1 + (Math.floor(((this.accordianBox.width - this.itemBoxes[0].width) / 2) / this.itemBoxes[0].width) * 2);

    if(visibleSlides < 1) visibleSlides = 1;

    this.visibleSlidesWidth = this.itemBoxes[0].width * visibleSlides;    

    let currVisibleByDots = (latestPointAdded * -1) + this.visibleSlidesWidth,
        finalVisibility = dots[dots.length - 1] * -1;

    if(
      widthLeftToAdd > this.accordianBox.width && 
      currVisibleByDots < this.containBox.width && 
      (latestPointAdded === 0 ||
      currVisibleByDots - this.itemBoxes[0].width < finalVisibility)
    ){
      if(this.itemBoxes[i] === undefined) return result = false;

      let slideStart = this.itemBoxes[i].left - this.accordianBox.left - this.moved,
          slideSides = (this.accordianBox.width - this.itemBoxes[i].width) / 2,
          slideMiddle = slideStart - slideSides;

      if(latestPointAdded === 0){
        let firstVisibleSlides = Math.floor(this.accordianBox.width / this.itemBoxes[0].width);
        if(firstVisibleSlides === 0) firstVisibleSlides = 1;
        
        this.firstVisibleSlidesWidth = this.itemBoxes[0].width * firstVisibleSlides;

        slideMiddle = (this.firstVisibleSlidesWidth + (this.visibleSlidesWidth / 2)) - (this.accordianBox.width / 2);
      } else if(slideMiddle < this.visibleSlidesWidth + (latestPointAdded * -1)){
        return result = false;
      } else if(slideStart < currVisibleByDots){
        return result = false;
      }

      let result = {
        middle: slideMiddle * -1
      }

      if(result.middle < dots[dots.length - 1]) result = false; // if new dot is further out than end dot

      return result;
    } else {
      return false;
    }
  }

  pressed(e){
    this.setState({
      pressing: true
    });

    this.start.x = e.pageX + 1 || e.touches[0].pageX + 1;
    this.start.relative = this.start.x - this.moved;
  }

  release(e){
    this.setState({
      pressing: false
    });

    if(this.state.dots.length > 0 && this.snapToPlace){
      let widthCheck = this.visibleSlidesWidth,
          slideCheck = false,
          currSlide = false;

      if(this.currSlide === 0) widthCheck = this.firstVisibleSlidesWidth;

      for(let i = 0; i < this.dotEls.length; i++){
        slideCheck = this.checkSnap(i, widthCheck);

        if(slideCheck !== false){
          currSlide = slideCheck;
        } else {
          break;
        }
      }

      if(currSlide !== false) this.currSlide = currSlide;

      this.dotEls[this.currSlide].classList.add("-active");

      this.moved = this.state.dots[this.currSlide];
      TweenLite.to(this.containEl, 0.7, {x: this.moved + "px"});
    }
  }

  checkSnap(i, widthCheck){
    if(
      this.currSlide < this.state.dots.length - i - 1 && // not at end dot
      this.moved - ((widthCheck / 2) * this.snapThreshold) < this.state.dots[this.currSlide + i] - (widthCheck / 2) // halfway to next point plus extra threshold
    ){
      this.dotEls[this.currSlide].classList.remove("-active");
      return this.currSlide + i + 1;
    } else if(
      this.currSlide - i > 0 && 
      this.moved + ((widthCheck / 2) * this.snapThreshold) > this.state.dots[this.currSlide - i] + (widthCheck / 2)
    ){
      this.dotEls[this.currSlide].classList.remove("-active");
      return this.currSlide - i - 1;
    } else{
      return false;
    }
  }

  moving(e){
    if(!this.state.pressing || this.containBox === undefined || this.state.dots === false) return;

    let x = e.pageX + 1 || e.touches[0].pageX + 1;

    this.moved = (x - this.start.relative);
    this.moved += (x - this.start.x) * this.extraMove; // add some inertia effect
    
    // prevent from going over edges
    // if(this.moved > 0) this.moved = 0;
    // if(this.moved < this.stopPoint) this.moved = this.stopPoint;
    
    TweenLite.to(this.containEl, 0.1, {x: this.moved + "px"});
  }

  paginationClicked(i){
    this.dotEls[this.currSlide].classList.remove("-active");
    this.currSlide = i;
    this.dotEls[this.currSlide].classList.add("-active");

    let x = this.state.dots[i];

    TweenLite.to(this.containEl, this.timing, {x: x + "px", ease: Sine.easeOut});
    this.moved = x;
  }

  render() {
    const data = this.props.data;

    return(
      <div className={`accordian ${data.class}${this.state.pressing ? ' -grabbing' : ''}`} ref={this.setUp.bind(this)}
        onMouseUp={e => this.release(e)} onTouchEnd={e => this.release(e)}
      >
        <div 
          className={`accordian-contain ${data.class}-contain`} 
          onMouseDown={e => this.pressed(e)} onTouchStart={e => this.pressed(e)}
          onMouseMove={e => this.moving(e)} onTouchMove={e => this.moving(e)}
        >

          {this.props.children}

        </div>

        {this.state.dots ? (
          <div className={`accordian-pagination ${data.class}-pagination`}>
            {this.state.dots.map((dot, i) => {
              return(
                <div className={`accordian-dot ${data.class}-dot`} onClick={() => this.paginationClicked(i)}/>
              )
            })}
          </div>
        ) : (
          null
        )}
      </div>
    )
  }
}


