import React, { Component } from 'react';

export default class SplitScreen extends Component {
  sideClicked(i){
    const halvesEl = document.querySelector(".module-halves"),
          slideShow = 20;

    let transform = slideShow;

    if(i === 1) transform *= -1;

    halvesEl.style.transform = `translateX(${transform}%)`
  }

  render() {
    const sides = this.props.data.sides;

    return (
      <section id={this.props.id} className="module-halves" style={{backgroundImage: `url(${this.props.data.bcg})`}}>
        {sides.map((side, i) => {
          if(side.paragraphs){
            return(
              <div className={`module-side ${side.theme}`}>
                <div className="col-5 col-s-12">
                  <div className={`module-content -align-${side.align}`}>
                    {side.logo && (
                      <img className="module-side-img" src={side.logo}/>
                    )}

                    {side.title && (
                      side.title.map(line => {
                        return(
                          <h3 className="module-subtitle -bold" data-fadeletters>{line}</h3>
                        )
                      })
                    )}
                    
                    {side.paragraphs.map(paragraph => {
                      return(
                        <p className="module-paragraph" data-fadeletters>{paragraph}</p>
                      )
                    })}
                  </div>
                </div>
                <div className="col-1 col-s-0"></div>
              </div>
            )
          } else if(side.logo){
            return(
              <div className={`module-side ${side.theme}`}>
                <div className={`col-5 col-s-12 -align-${side.align}`}>
                  {side.logo && (
                    <img className="module-side-img" src={side.logo}/>
                  )}
                </div>
                <div className="col-1 col-s-0"></div>
              </div>
            )
          } else if(side.quote){
            return(
              <div className={`module-side ${side.theme}`}>
                <div className="col-5 col-s-12">
                  <div className={`module-content -align-${side.align}`}>
                    <h3 className="quote-text -bold" data-fadeletters>{side.quote.text}</h3>
                    {side.quote.source.map(source => {
                      return(
                        <p className="quote-source" data-fadeletters>{source}</p>
                      )
                    })}
                  </div>
                </div>
                <div className="col-1 col-s-0"></div>
              </div>
            )
          } else if(side.image){
            return(
              <div className="module-side" style={{backgroundImage:`url(./images/${side.image})`, minHeight: "75vh"}}/>
            )
          } else if(side.bigTitle){
            return(
              <div className={`module-side ${side.theme}`}>
                <div className="col-5 col-s-12">
                  <div className={`module-content -align-${side.align}`}>
                    <h2 className="module-title" data-fadeletters>{side.bigTitle}</h2>
                  </div>
                </div>
                <div className="col-1 col-s-0"></div>
              </div>
            )
          } else {
            return(
              <div className={`module-side col-s-0 ${side.theme}`}/>
            )
          }

          
        })}

        {(this.props.children !== undefined &&
          this.props.children
        )}
      </section>
    );
  }
}


