import React, { Component } from 'react';
import { TweenLite } from "gsap/TweenMax";
import { Link } from 'react-router-dom';

export default class BrandGuidelines extends Component {
  componentDidMount(){
    this.bcgs = document.querySelectorAll(".home-side-bcg");
    TweenLite.to(this.bcgs, 0, {opacity: 0.5});

    this.copy = document.querySelectorAll(".home-copy");
    this.paragraphs = document.querySelectorAll(".home-paragraph");
    this.paragraphs.forEach((paragraph, i) => {
      paragraph.style.height = paragraph.clientHeight + "px";
      paragraph.classList.add("-hidden");
      this.copy[i].classList.add("-active");
    })
  }

  show(i){
    TweenLite.to(this.bcgs[i], 0.4, {opacity: 1});
    this.paragraphs[i].classList.remove("-hidden");
  }

  hide(i){
    TweenLite.to(this.bcgs[i], 0.4, {opacity: 0.5});
    this.paragraphs[i].classList.add("-hidden");
  }
  
  render() {
    return(
      <main>
        <div className="home-side-bcg" onMouseEnter={() => this.show()} onMouseLeave={() => this.hide()}>
          <iframe src="https://player.vimeo.com/video/307136859?background=1" frameBorder="0"/>
        </div>
        <div className="home-side-bcg" onMouseEnter={() => this.show()} onMouseLeave={() => this.hide()}>
          <iframe src="https://player.vimeo.com/video/307136881?background=1" frameBorder="0"/>
        </div>

        <section className="module fullScreen home">
          <div className="sideNav-border"></div>

          <Link to="/story" className="home-side" onMouseEnter={() => this.show(0)} onMouseLeave={() => this.hide(0)}>
            <div className="home-copy">
              <h3 className="home-heading">BRAND STORY</h3>
              <p className="home-paragraph">These guidelines are designed to help you share with the world what a 100% Pure New Zealand experience feels like.</p>
              <a href="javascript:void(0)" className="home-more">More<i/></a>
            </div>
          </Link>
          <Link to="/guidelines" className="home-side" onMouseEnter={() => this.show(1)} onMouseLeave={() => this.hide(1)}>
            <div className="home-copy">
              <h3 className="home-heading">BRAND GUIDELINES</h3>
              <p className="home-paragraph">This guide is to help you as the story tellers of Tourism New Zealand. A way to better understand the deeper meaning and richer expression that now defines our story.</p>
              <a href="javascript:void(0)" className="home-more">More<i/></a>
            </div>
          </Link>
        </section>
      </main>
    )
  }
}


