import React, { Component } from 'react';
import Accordian from './Accordian';

export default class PeoplePlaceCarousel extends Component {
  carouselSegments = [
    // "./images/carousel/01.svg",
    "./images/carousel/02.svg",
    "./images/carousel/03.svg",
    "./images/carousel/04.svg",
    // "./images/carousel/05.svg"
  ]

  render() {
    return(
      <section id={this.props.id} className="module -dark-theme" style={{padding: 0, 	minHeight: "unset", maxHeight: "90vw"}}>
        <Accordian data={{class: 'peoplePlace', start: 0.5}}>
          {this.carouselSegments.map((segment, i) => {
            return(
              <img src={`${segment}`}/>
            )
          })}
        </Accordian>
      </section>
    )
  }
}


