import React, { Component } from 'react';

export default class ToolTips extends Component {
  componentDidMount(){
    this.data = this.props.data;
    
    const paragraphs = document.querySelectorAll("p[data-fadeletters]");

    paragraphs.forEach(para => {
      para.querySelectorAll(".-word").forEach(word => {

        let wordText = word.innerHTML.toLowerCase();

        this.data.forEach(tooltip => {
          if(wordText.indexOf(tooltip.word.toLowerCase()) === 0){
            word.setAttribute('tooltip', tooltip.definition);
          }
        })

      })
    })
  }

  render() {
    return(
      <React.Fragment></React.Fragment>
    )
  }
}


