import React, { Component } from 'react';
import Accordian from './Accordian';

export default class QuoteCarousel extends Component {
  componentDidUpdate(){
    let allText = document.querySelectorAll(".quotes-carousel-text"),
        mostHeight = 0;

    allText.forEach(text => {
      let heightTest = text.clientHeight;

      if(heightTest > mostHeight){
        mostHeight = heightTest;
      }
    })

    let quoteHeight = document.querySelector(".quotes-carousel-speechmark").clientHeight;

    document.querySelector(".quotes-carousel").style.height = mostHeight + quoteHeight + "px";
  }

  render() {
    const title = this.props.data.title,
          quotes = this.props.data.quotes;

    return(
      <section id={this.props.id} className="module quotes" style={{backgroundImage: "url(./images/roys-peak.png)", backgroundColor: "transparent"}}>
        <div className="quotes-top">
          <div className="col-3 col-s-12">
            <h3 className="quotes-title" data-fadeletters>{title}</h3>
          </div>
          <div className="col-9 col-s-0"></div>
        </div>
        <Accordian data={{class: 'quotes-carousel'}}>
          {quotes.map((quote, i) => {
            return(
              <div className="quotes-carousel-item">
                <p className="quotes-carousel-speechmark">&ldquo;</p>
                <p className="quotes-carousel-text" data-fadeletters>{quote}</p>
              </div>
            )
          })}
        </Accordian>
      </section>
    )
  }
}


