import React, { Component } from 'react';
import $ from "jquery";
import fontfaceobserver from "fontfaceobserver";

export default class FadeLetters extends Component {
  componentDidMount(){
    this.$fadeletters = $("[data-fadeletters]");
    this.lines = [],
    this.windowH = window.screen.height;
    this.windowW = window.screen.width;
    this.fonts = [];
    this.loadFailCount = 0;

    $(() => {
      this.init();
    })
  }

  init(){
    console.log('split started');

    this.loadFonts().then(() => {
      console.log('fonts loaded');

      this.splitParagraph();
      
      this.checkResizingDone(() => {
        setTimeout(() => {
          this.splitParagraph();
        },400)
      })

      $(window).on("orientationchange",() => {
        setTimeout(() => {
          this.splitParagraph();
        },400)
      })
    }).catch(e => {
      this.loadFailCount = this.loadFailCount + 1;
      // fails to detect fonts on very slow speeds sometimes
      this.init();
    })
  }

  loadFonts(){
    return new Promise((resolve, reject) => {
      if(this.fonts.length === 0){
        this.$fadeletters.map((i,el) => {
          let elFont = $(el).css("font-family");

          if(!this.fonts.includes(elFont)){
            elFont = elFont.split(", ");
            elFont.forEach(font => {
              this.fonts.push(font.replace(/['"]+/g, '')); // remove quotes from font name
            })
          }
        })
      }

      this.fonts.forEach((font, i) => {
        console.log(font);

        let loadFont = new fontfaceobserver(font);

        loadFont.load().then(() => {
          this.fonts.splice(this.fonts.indexOf(font), 1);

          console.log(this.fonts);

          if(this.fonts.length === 0){
            resolve();
          }
        }).catch(e => {
          if(this.loadFailCount > 0){ // temp fix
            this.fonts.splice(this.fonts.indexOf(font), 1);

            if(this.fonts.length === 0){
              resolve();
            }
          } else {
            reject(e);
          }
        })
      })
    })
  }

  checkResizingDone(cb){
    $(window).resize(() => {
      let currH = window.screen.height,
          currW = window.screen.width;

      if(currH !== this.windowH || currW !== this.windowW){
        setTimeout(() => {
          this.windowH = window.screen.height;
          this.windowW = window.screen.width;
  
          if(this.windowW === currW && this.windowH === currH){
            cb();
          }
        },400);
      }
    })
  }

  splitParagraph(){
    const self = this;

    this.props.start();

    this.$fadeletters.map((i, paragraph) => {
      let $paragraph = $(paragraph),
          words = $paragraph.text().split(' '),
          lines = [],
          currLineY = 0;

      $paragraph.html('');
      
      words.map((word) => {
        let span = `<span class="-word">${word}</span> `;

        $paragraph.append(span);
      });

      let paragraphSpans = $paragraph.children('span');

      paragraphSpans.map((j, span) => {
        let y = $(window).scrollTop() + span.getBoundingClientRect().top;
  
        if(y > currLineY){
          currLineY = y;
          lines.push({y: y, text: ''});
        }

        let spanHTML = span.innerHTML;

        // if(j === paragraphSpans.length - 2) spanHTML += "&nbsp;";

        let spanEl = `<span class="-word">${spanHTML}</span> `;

        lines[lines.length - 1].text += spanEl;
      });

      $paragraph.html('');

      lines.map((line) => {
        let lineEl = `<p class="-line" data-height=${line.y}>${line.text}</p>`

        $paragraph.append(lineEl);
        $paragraph.css({opacity: 1});
      }) 
    })

    this.lines = [];

    $(window).off('scroll','.-line');

    this.$fadeletters.children('.-line').map((i, line) => {
      let height = $(line).data('height'),
          active = false;

      if($(window).scrollTop() + this.windowH > height){
        active = true;
        $(line).addClass("-active");
      }

      this.lines.push({
        el:line,
        y: height,
        active: active
      })

      $(window).scroll(() => self.fadeUpParagraphs(i));
    })

    this.props.end();
  }

  fadeUpParagraphs(i){
    if(this.lines[i] && !this.lines[i].active && $(window).scrollTop() + (this.windowH * 0.75) > this.lines[i].y){
      this.lines[i].active = true;

      $(this.lines[i].el).addClass('-active');
    }
  }

  render() {
    return (
      <React.Fragment></React.Fragment>
    );
  }
}


